body{
    background-color:#F2F2F2;
}

.cx-sec1{
    background-color:#BFBFBF;
}

.cx-texto-horizontal{
    padding: 75px 0 75px 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.cx-texto-horizontal > h5{
    margin-right: 100px;
    font-weight: bold;
}

.cx-texto-horizontal > p{
    width: 640px;
}

.cx-sec2{
    position: relative;
    display: block;
    width: 100%;
    height: 670px;
}

.fundo-escuro{
    width: 100%;
    height: 200px;
    background: black;
}

.cx-horizontal1{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.cx-imagem-vertical1, .cx-texto-vertical{
    height: 100%;
    width: 500px;
}

.cx-imagem-vertical1{
    position: absolute;
    top: 150px;
    left: 200px;
}

.cx-texto-vertical{
    position: relative;
}

.cx-texto-vertical > h2{
    margin: 150px 100px 20px 10px;
    font-weight: normal;
}

.cx-texto-vertical > p{
    margin: 10px 100px 20px 10px;
}

.cx-texto-vertical > a{
    display: block;
    text-align: center;
    margin: 50px 0 20px 10px;
    width: 300px;
    padding: 10px;
    border-radius: 3px;
    font-size: 20px;
    border: 2px solid rgb(39, 37, 37);
    color: white;
    background-color: black;
    text-decoration: none;
}

.cx-texto-vertical > a:hover{
    background-color: white;
    color: black;
}

.cx-sec3{
    display: flex;
    margin-top: 230px;
    width: 100%;
    height: 800px;
}
.cx-imagem-vertical2{
    height: 800px;
    width: 625px;
}

.cx-coluna{
    position: relative;
    display: block;
    height: 800px;
    width: 625px;
}

.cx-imagem-quadrada1{
    width: 50%;
    margin: 20px auto 30px auto;
}

.cx-quadrada{
    position: relative;
    margin: 20px auto 30px auto;
    background-color: black;
    height: 460px;
    width: 550px;
    color: white;
}

.cx-quadrada > h5{
    font-size: 20px;
    padding: 150px 0 10px 20px;
}

.cx-quadrada > p{
    font-size: 35px;
    padding: 0 0 10px 20px;
}

.cx-texto{
    position: relative;
    display: block;
    height: 800px;
    width: 625px;
}

.cx-textoFilho{
    margin-top: 200px;
}

.cx-textoFilho > p{
    padding: 10px;
}

.cx-sec4{
    position: relative;
    margin-bottom: 100px;
}

.cx-botao{
    margin-top: 100px;
    margin-bottom: 30px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.cx-botao > button{
    border: none;
    border-bottom: 1px solid black;
    padding: 10px;
    border-width: medium;
    font-size: 30px;
    margin: 0 30px 0 30px;
}

.cx-botao > button:hover{
   background-color: black;
   border: none;
   padding: 10px;
   border-radius: 30px;
   color: white;
   cursor: pointer;
}

#cx-horizontal2{
    margin-top: 20px;
    width: 100%;
    height: 380px;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    display:flex;
}

.cx-imagem-quadrada2 > img{
    width:425px;
    height:330px;
}

.cx-texto-coluna{
   display: block;
   width: 550px;
   height: 380px;
   margin: 50px 0 0 100px;
}

.cx-texto-coluna > h2{
    font-size: 30px;
}

.cx-texto-coluna > p{
     margin-top: 20px;
}

.cx-texto-coluna > ul{
    margin: 20px 0 0 20px;
}

.cx-texto-coluna > ul > li{
    font-size: 20px;
}

#cx-texto-block1{
    margin: 50px auto;
    width: 80%;
    display: none;
}

#cx-texto-block1 > p{
    margin: 20px auto 0 auto;
    width: 40%;
}

#cx-texto-block2{
    width: 100%;
    display: none;
}

#cx-texto-block2 > h2, #cx-texto-block2 > p{
    margin: 40px auto 0 auto;
    text-align: center;
}

#cx-texto-block2 > a{
    display: block;
    margin: 40px auto 0 auto;
    width: 300px;
    text-align: center;
    padding: 10px;
    background-color:black;
    border: 1px solid black;
    color: white;
    border-radius: 3px;
    text-decoration: none;
}

#cx-texto-block2 > a:hover{
    background-color: white;
    color: black;
}

.cx-sec5{
    background-color:#BFBFBF;
    height: 100%;
}

.cx-fotos > img:hover{
    border: 2px solid rgba(122, 122, 122, 0.26);
    cursor: pointer;
}

.cx-fotos-absolut{
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    display: none;
}

.cx-fotos-absolut > span{
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 0 20px 10px 20px;
    border-radius: 50%;
    color: black;
    font-size: 35px;
    cursor: pointer;
}

.cx-fotos-absolut > span:hover{
    color: red;
}


.cx-fotos-absolut > img{
    position: absolute;
    top: 20%;
    left: 25%;
    width: 1000px;
    height: 600px;
}

.cx-contato{
    width: 100%;
    height: 500px;
}

.cx-contato-filha{
    width: 50%;
    margin: 50px auto;
}


form > input{
    margin: 30px 15px 30px 0;
    padding: 10px;
    border: 1px solid black;
    border-radius: 10px;
    width: 30%;
    font-size: 20px;

}


form > textarea{
    margin: 30px 15px 0 0;
    padding: 10px;
    border: 1px solid black;
    border-radius: 10px;
    width: 100%;
    height: 150px;
    font-size: 20px;
    margin-bottom: 15px;
}

.enviar:hover{
    background-color: rgba(128, 128, 128, 0.801);
    color: white;
    cursor: pointer;
}

@media screen and (max-width:768px){
    
    .cx-texto-horizontal > p{
        width: 400px;
    }

    .cx-sec2{
        height: 670px;
    }
    
    .cx-imagem-vertical1, .cx-texto-vertical{
        width: 400px;
    }
    
    .cx-imagem-vertical1{
        left: 0;
    }

    .cx-imagem-vertical1 > img{
        width: 350px;
        height: 400px;
    }

    .cx-texto-vertical{
        position: absolute;
        top: 100px;
        right: 0;
    }

    .cx-imagem-vertical2{
        display: none;
    }

    .cx-sec3{
        display: flex;
        margin-top: 50px;
        width: 100%;
        height: 300px;
    }
    
    .cx-coluna{
        height: 100%;
        width: 100%;
    }
    
    .cx-imagem-quadrada1{
      display: none;
    }
    
    .cx-quadrada{
        width: 100%;
    }

    .cx-quadrada > p{
        width: 75%;
    }
    
    .cx-texto{
        display: none;
    }
    
    .cx-botao{
        margin-top: 200px;
        margin-bottom: 30px;
    }
    
    #cx-horizontal2{
        height: 200px;
    }
    
    .cx-imagem-quadrada2 > img{
        display: none;
    }
    
    .cx-texto-coluna{
       height: 200px;
    }
    
    #cx-texto-block1 > p{
        margin: 60px auto 0 auto;
        width: 500px;
    }

    .cx-fotos{
        display: none;
    }

    .cx-contato{
        width: 100%;
        height: 600px;
    }
    
    .cx-contato-filha{
        width: 100%;
    }

    form{
        padding-top: 50px;
    }

    form > h1{
        margin: 30px 0 30px 30px;
    }
    
    
    form > input{
        margin: 30px 0 30px 30px;
        width: 25%;
    }
    
    
    form > textarea{
        margin: 30px 15px 0 20px;
        width: 80%;
    }
    
    
}

@media screen and (max-width:500px){
    
    .cx-sec2, .cx-sec4{
        display: none;
    }

    .cx-texto-horizontal{
        padding: 40px 0 40px 30px;
        display: block;
    }
    
    .cx-texto-horizontal > h5{
        margin-right: 0;
    }
    
    .cx-texto-horizontal > p{
        width: 300px;
    }

    .cx-sec3{
        margin-top: 0;
        width: 100%;
        height: 300px;
    }
    
    .cx-coluna{
        height: 100%;
        width: 100%;
    }
    
    .cx-quadrada{
        width: 100%;
        margin: 0 auto 30px auto;
    }

    .cx-quadrada > p{
        width: 90%;
        font-size: 35px;
    }
    
    .cx-quadrada > h5{
        font-size: 20px;
        padding: 50px 0 10px 20px;
    }

    .cx-contato{
        width: 100%;
        height: 100%;
        display: block;
    }
    
    .cx-contato-filha{
        width: 100%;
        height: 860px;
        margin: 0;
        display: block;
    }

    form{
        padding-top: 180px;
        display: block;
    }

    form > h1{
        margin: 30px 0 30px 30px;
    }
    
    
    form > input{
        margin: 10px auto 30px auto;
        width: 80%;
        font-size: 20px;
        display: block;
    
    }
    
    
    form > textarea{
        margin: 10px auto 30px auto;
        width: 80%;
        height: 150px;
        margin-bottom: 15px;
        display: block;
    }
    
}