.header{
    position: relative;
    background-image: url(../img/header/fundoHeader.png);
    width: 100%;
    height: 620px;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

.cx-logo{
    position: absolute;
    top: 50px;
    left: 100px;
}

.logo{
    border-radius: 50%;
}

.cx-retangulo{
    position: absolute;
    left: 50%;
    width: 460px;
    height: 100%;
    background-color: black;
    color:white;
}

.cx-retangulo > h1{
    margin: 150px 0 20px 10px;
}

.cx-retangulo > h5, .cx-retangulo > p{
    margin: 10px 0 20px 10px;
}

.cx-retangulo > a{
    display: block;
    text-align: center;
    margin: 50px 0 20px 10px;
    width: 300px;
    padding: 10px;
    border-radius: 3px;
    font-size: 20px;
    border: 2px solid white;
    color:white;
    text-decoration: none;
}

.cx-retangulo > a:hover{
    background-color: white;
    color: black;
}

@media screen and (max-width:768px){
    
    .cx-logo{
        top: 50px;
        left: 10px;
    }
    
    .cx-retangulo{
        left: 50%;
        width: 384px;
    }
    
    .cx-retangulo > h1{
        font-size: 30px;
    }
}

@media screen and (max-width:500px){

    .header{
        height: 300px;
    }
    
    
      .cx-logo{
        display: block;
        top: 20px;
        left: 38px;
    }
    
    .cx-retangulo{
        display: none;
    }
}
    
